* {
	box-sizing: border-box;
}

body {
	color: $dark;
}

.container {
	margin: 0 auto;
	padding: 0 var(--container-offset);
	max-width: var(--container-width);
}

.section {
	margin-bottom: 80px;
}

@media screen and (max-width: $laptop) {
	.section {
		margin-bottom: 64px;
	}
}

@media screen and (max-width: $tablet) {
	.container {
		padding: 0 16px;
	}
}

@media screen and (max-width: $mob) {
	.section {
		margin-bottom: 40px;
	}
}